import React, { Component } from "react";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import DropdownSelect from "../../components/InputElement/Dropdown";
import InputElement from "../../components/InputElement/InputElement";
export default class RegistrationForm extends Component {
  render() {
    return (
      <div style={{ marginTop: 30 }}>
        <CardLayout
          name="Beneficiary Form"
          navigation={true}
          navigationRoute="/registrationList"
        >
          <FormGroup row />
          <div style={{ margin: 20 }}>
            <FormGroup row>
              <Col xs="12" md="5">
                <InputElement
                  type="text"
                  label="Name"
                  name="userName"
                  placeholder="Please enter name "
                />
              </Col>
              <Col md="5">
                <InputElement
                  type="text"
                  label="Phone Number"
                  name="contactNumber"
                  placeholder="Please enter phone number "
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs="12" md="5">
                <InputElement
                  type="number"
                  label="Age"
                  name="age"
                  placeholder="Please enter age "
                />
              </Col>
              <Col md="5">
                <Label>Gender</Label>
                <DropdownSelect name="gender" placeholder="Select gender " />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs="12" md="5">
                <Label>State</Label>
                <DropdownSelect name="state" placeholder="Select state " />
              </Col>
              <Col md="5">
                <Label>District</Label>
                <DropdownSelect
                  name="district"
                  placeholder="Select district "
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs="12" md="5">
                <Label>Grampanchayat</Label>
                <DropdownSelect
                  name="grampanchayat"
                  placeholder="Select grampanchayat "
                />
              </Col>
              <Col md="5">
                <Label>Village</Label>
                <DropdownSelect name="village" placeholder="Select village " />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs="12" md="5">
                <InputElement
                  type="text"
                  label="Aadhaar number"
                  name="aadhaarNumber"
                  placeholder="Please enter aadhaar number "
                />
              </Col>
              <Col md="5">
                <Label>Role</Label>
                <DropdownSelect name="role" placeholder="Select role " />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs="12" md="5">
                <InputElement
                  type="password"
                  label="Password"
                  name="password"
                  placeholder="Please enter password"
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs="4" md="2">
                <Button className="theme-positive-btn">Submit</Button>
              </Col>
              <Col md="2">
                <Button className="theme-reset-btn">Reset</Button>
              </Col>
            </FormGroup>
          </div>
        </CardLayout>
      </div>
    );
  }
}



